import _ from 'lodash'

interface SuccessfulSafeJsonParse {
  status: 'ok'
  json: ReturnType<JSON['parse']>
}

interface FailedSafeJsonParse {
  status: 'error'
  message: string
}

export const safeJsonParse = (s: string): SuccessfulSafeJsonParse | FailedSafeJsonParse => {
  try {
    const json = JSON.parse(s)
    return {status: 'ok', json}
  } catch (error) {
    if (error instanceof SyntaxError) {
      return {status: 'error', message: error.message}
    }
    throw error
  }
}

// TODO: remove todo

interface KeyValue {
  key: string
}
interface LeftValue {
  type: 'left'
  value: unknown
}

type LeftKeyValue = LeftValue & KeyValue

interface BothValue {
  type: 'both'
  value: null | string | number | boolean | JsonArray | JsonObject
}

type BothKeyValue = BothValue & KeyValue

interface RightValue {
  type: 'right'
  value: unknown
}

type RightKeyValue = RightValue & KeyValue

interface ComplexValue {
  type: 'complex'
  value: JsonArray | JsonObject
}

type ComplexKeyValue = ComplexValue & KeyValue

export interface JsonRootSame {
  type: 'same'
  both: BothValue
}

export interface JsonRootDiffer {
  type: 'differ'
  left: LeftValue
  right: RightValue
}

export interface JsonArray {
  type: 'array'
  isSame: boolean
  array: Array<LeftValue | BothValue | RightValue | ComplexValue>
}

export interface JsonObject {
  type: 'object'
  isSame: boolean
  array: Array<LeftKeyValue | BothKeyValue | RightKeyValue | ComplexKeyValue>
}

type JsonRoot = JsonRootSame | JsonRootDiffer | JsonArray | JsonObject

export interface SuccessfulCompare {
  status: 'ok'
  leftJson: ReturnType<JSON['parse']>
  rightJson: ReturnType<JSON['parse']>
  result: JsonRoot
}

export interface FailedCompare {
  status: 'error'
  left: string
  right: string
  leftMessage?: string
  rightMessage?: string
}

export type Compare = SuccessfulCompare | FailedCompare

const tupleArray = (...v: string[]): string[] => v 

const JSON_VALUE_PRIMITIVE_TYPES = ['number', 'string', 'boolean', 'null'] as const
const JSON_VALUE_PRIMITIVE_TYPES_INFERED = tupleArray(...JSON_VALUE_PRIMITIVE_TYPES)
// const JSON_VALUE_TYPES = ['array', 'object', ...JSON_VALUE_PRIMITIVE_TYPES] as const

interface JsonRawValueNull {
  type: 'null'
  value: null
}

interface JsonRawValueString {
  type: 'string'
  value: string
}

interface JsonRawValueNumber {
  type: 'number'
  value: number
}

interface JsonRawValueBoolean {
  type: 'boolean'
  value: boolean
}

interface JsonRawValueArray {
  type: 'array'
  value: unknown[]
}

interface JsonRawValueObject {
  type: 'object'
  value: Record<string, unknown>
}

type JsonRawValue = JsonRawValueNull | JsonRawValueString | JsonRawValueNumber | JsonRawValueBoolean | JsonRawValueArray | JsonRawValueObject

const addType = (value: unknown): JsonRawValue => {
  if (value === null) return {type: 'null', value: null}
  if (typeof value === 'number') return {type: 'number', value}
  if (typeof value === 'string') return {type: 'string', value}
  if (typeof value === 'boolean') return {type: 'boolean', value}
  if (_.isArray(value)) return {type: 'array', value}
  if (_.isPlainObject(value)) return {type: 'object', value: value as Record<string, unknown>}
  throw Error('Unexpected type...')
}

const isPrimitiveType = (v: JsonRawValue): v is JsonRawValueNull | JsonRawValueString | JsonRawValueNumber | JsonRawValueBoolean =>
  JSON_VALUE_PRIMITIVE_TYPES_INFERED.includes(v.type)
  
const toLeftRightValues = (a: unknown[], type: 'left' | 'right'): Array<LeftValue | RightValue> =>
   a.map((value) => ({type, value}))

const compareArrays = (leftArray: unknown[], rightArray: unknown[], isSame = true, values: JsonArray['array'] = []): JsonArray => {
  if (leftArray.length === 0) {
    return {
      type: 'array',
      isSame: isSame && rightArray.length === 0,
      array: [...values, ...toLeftRightValues(rightArray, 'right')]
    }
  }

  if (rightArray.length === 0) {
    return {
      type: 'array',
      isSame: false,
      array: [...values, ...toLeftRightValues(leftArray, 'left')]
    }
  }

  const leftTypedValue = addType(leftArray[0])
  const rightTypedValue = addType(rightArray[0])

  const c = compareValues(leftTypedValue, rightTypedValue)

  switch (c.type) {
    case 'same':
      return compareArrays(leftArray.slice(1), rightArray.slice(1), isSame, [...values, c.both])
    case 'differ':
      return compareArrays(leftArray.slice(1), rightArray, false, [...values, c.left])
    case 'array':
    case 'object':
      return compareArrays(leftArray.slice(1), rightArray.slice(1), isSame && c.isSame, [...values, {type: 'complex', value: c}])
  }
}

const toLeftRightKeyValues = (a: Array<[string, unknown]>, type: 'left' | 'right'): Array<LeftKeyValue | RightKeyValue> =>
   a.map(([key, value]) => ({type, key, value}))

const compareKeyArrays = (leftKeyArray: Array<[string, unknown]>, rightKeyArray: Array<[string, unknown]>, isSame = true, values: JsonObject['array'] = []): JsonObject => {
  if (leftKeyArray.length === 0) {
    return {
      type: 'object',
      isSame: isSame && rightKeyArray.length === 0,
      array: [...values, ...toLeftRightKeyValues(rightKeyArray, 'right')]
    }
  }

  if (rightKeyArray.length === 0) {
    return {
      type: 'object',
      isSame: false,
      array: [...values, ...toLeftRightKeyValues(leftKeyArray, 'left')]
    }
  }

  const [leftKey, leftValue] = leftKeyArray[0]
  const [rightKey, rightValue] = rightKeyArray[0]

  if (leftKey !== rightKey) {
    return compareKeyArrays(leftKeyArray.slice(1), rightKeyArray, false, [...values, { type: 'left', key: leftKey, value: leftValue }])
  }

  const leftTypedValue = addType(leftValue)
  const rightTypedValue = addType(rightValue)

  const c = compareValues(leftTypedValue, rightTypedValue)

  switch (c.type) {
    case 'same':
      return compareKeyArrays(leftKeyArray.slice(1), rightKeyArray.slice(1), isSame, [...values, {key: leftKey, ...c.both}])
    case 'differ':
      return compareKeyArrays(leftKeyArray.slice(1), rightKeyArray, false, [...values, {key: leftKey, ...c.left}])
    case 'array':
    case 'object':
      return compareKeyArrays(leftKeyArray.slice(1), rightKeyArray.slice(1), isSame && c.isSame, [...values, { type: 'complex', key: leftKey, value: c }])
  }
}

const compareObjects = (leftObject: Record<string, unknown>, rightObject: Record<string, unknown>): JsonObject => {
  const leftKeyArray = _.sortBy(_.toPairs(leftObject), ([k, _]) => k)
  const rightKeyArray = _.sortBy(_.toPairs(rightObject), ([k, _]) => k)

  return compareKeyArrays(leftKeyArray, rightKeyArray)
}

const compareValues = (typedLeftJson: JsonRawValue, typedRightJson: JsonRawValue, _path: Array<string | number> = []): JsonRoot => {
  if (typedLeftJson.type === typedRightJson.type) {
    if (isPrimitiveType(typedLeftJson)) {
      if (typedLeftJson.value === typedRightJson.value) {
        return {
          type: 'same',
          both: {
            type: 'both',
            value: typedLeftJson.value
          }
        }
      } else {
        return {
          type: 'differ',
          left: {
            type: 'left',
            value: typedLeftJson.value,
          },
          right: {
            type: 'right',
            value: typedRightJson.value,
          }
        }
      }
    }

    if (typedLeftJson.type === 'array' && typedRightJson.type === 'array') {
      return compareArrays(typedLeftJson.value, typedRightJson.value)
    }
    
    if (typedLeftJson.type === 'object' && typedRightJson.type === 'object') {
      return compareObjects(typedLeftJson.value, typedRightJson.value)
    }
  }

  return {
    type: 'differ',
    left: {
      type: 'left',
      value: typedLeftJson.value,
    },
    right: {
      type: 'right',
      value: typedRightJson.value,
    }
  }
}

export const compareJsons = (left: string, right: string): Compare => {
  const leftParsed = safeJsonParse(left)
  const rightParsed = safeJsonParse(right)

  if (leftParsed.status === 'error' || rightParsed.status === 'error') {
    return {
      status: 'error',
      left,
      right,
      leftMessage: leftParsed.status === 'error' ? leftParsed.message : undefined,
      rightMessage: rightParsed.status === 'error' ? rightParsed.message : undefined,
    }
  }

  const leftJson = leftParsed.json
  const rightJson = rightParsed.json

  return {
    status: 'ok',
    leftJson,
    rightJson,
    result: compareValues(addType(leftJson), addType(rightJson)),
  }
}
