import React, { useState } from 'react'
import _ from 'lodash'
import clsx from 'clsx'
import {compareJsons, FailedCompare, JsonArray, JsonObject} from '../utils'
import './DisplayDifference.scss'

const INDENTATION_SIZE = 2 as const
const INDENTATION_IN_SPACES = _.range(INDENTATION_SIZE).map(() => ' ').join('')

const addIndentation = (s = ''): string => `${INDENTATION_IN_SPACES}${s}`

interface DisplayDifferencePrefs {
  comparator: ReturnType<typeof compareJsons>
}

const DisplayError = ({failedCompare: {leftMessage, rightMessage}}: {failedCompare: FailedCompare}): React.ReactElement => (
  <div className="DisplayDifference DisplayError">
    {leftMessage && <pre>Could not parse left input: {leftMessage}</pre>}
    {rightMessage && <pre>Could not parse right input: {rightMessage}</pre>}
  </div>
)

interface PrefixValue {
  prefix?: string
  indentation?: string
  suffix?: string
}

const stringifyWithIndentation = (v: unknown, indentation = ''): string =>
  JSON.stringify(v, null, INDENTATION_SIZE).replaceAll('\n', `\n${indentation}`)

const DisplayLeftValue = ({indentation, prefix, suffix, value}: {value: unknown} & PrefixValue): React.ReactElement => (
  <div className="DisplayLeftValue">
    <pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}{stringifyWithIndentation(value, indentation)}{suffix}</pre>
  </div>
)

const DisplayRightValue = ({indentation, prefix, suffix, value}: {value: unknown} & PrefixValue): React.ReactElement => (
  <div className="DisplayRightValue">
    <pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}{stringifyWithIndentation(value, indentation)}{suffix}</pre>
  </div>
)

const DisplayBothValue = ({indentation, prefix, suffix, value}: {value: unknown} & PrefixValue): React.ReactElement => (
  <div className="DisplayBothValue">
    <pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}{stringifyWithIndentation(value, indentation)}{suffix}</pre>
  </div>
)

const DisplayArrayValue = ({indentation, prefix, suffix, collapse = true, value: {isSame, array}}: {value: JsonArray, collapse?: boolean} & PrefixValue): React.ReactElement => {
  const [collapsed, setCollapsed] = useState(collapse)

  if (array.length === 0) return <div className="DisplayArrayValue"><pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}[]</pre></div>
  if (collapsed) return <div className={clsx("DisplayCollapsedValue", {differ: !isSame})} onClick={() => setCollapsed(false)}><pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}[...]</pre></div>

  return (
    <div className="DisplayArrayValue">
      <pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}[</pre>
      {array.map((e, i) => {
        const suffix = i < array.length - 1 ? ',' : ''
        switch (e.type) {
          case 'left':
            return <DisplayLeftValue indentation={addIndentation(indentation)} value={e.value} suffix={suffix} />
          case 'right':
            return <DisplayRightValue indentation={addIndentation(indentation)} value={e.value} suffix={suffix} />
          case 'both':
            return <DisplayBothValue indentation={addIndentation(indentation)} value={e.value} suffix={suffix} />
          case 'complex':
            return e.value.type === 'array'
              ? <DisplayArrayValue indentation={addIndentation(indentation)} value={e.value} suffix={suffix} />
              : <DisplayObjectValue indentation={addIndentation(indentation)} value={e.value} suffix={suffix} />
        }
      })}
      <pre>{indentation}]{suffix}</pre>
    </div>
  )
}

const DisplayObjectValue = ({indentation, prefix, suffix, collapse = true, value: {isSame, array}}: {value: JsonObject, collapse?: boolean} & PrefixValue): React.ReactElement => {
  const [collapsed, setCollapsed] = useState(collapse)

  if (array.length === 0) return <div className="DisplayObjectValue"><pre>{'{}'}</pre></div>
  if (collapsed) return <div className={clsx("DisplayCollapsedValue", {differ: !isSame})} onClick={() => setCollapsed(false)}><pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}{'{'}...{'}'}</pre></div>

  return (
  <div className="DisplayObjectValue">
    <pre>{indentation}{prefix ? `${JSON.stringify(prefix)}: ` : ''}{'{'}</pre>
    {array.map((e, i) => {
      const suffix = i < array.length - 1 ? ',' : ''
      switch (e.type) {
        case 'left':
          return <DisplayLeftValue indentation={addIndentation(indentation)} prefix={e.key} value={e.value} suffix={suffix} />
        case 'right':
          return <DisplayRightValue indentation={addIndentation(indentation)} prefix={e.key} value={e.value} suffix={suffix} />
        case 'both':
          return <DisplayBothValue indentation={addIndentation(indentation)} prefix={e.key} value={e.value} suffix={suffix} />
        case 'complex':
          return e.value.type === 'array'
            ? <DisplayArrayValue indentation={addIndentation(indentation)} prefix={e.key} value={e.value} suffix={suffix} />
            : <DisplayObjectValue indentation={addIndentation(indentation)} prefix={e.key} value={e.value} suffix={suffix} />
      }
    })}
    <pre>{indentation}{'}'}{suffix}</pre>
  </div>
)
  }

const DisplayDifference = ({comparator}: DisplayDifferencePrefs): React.ReactElement => {
  if (comparator.status === 'error') return <DisplayError failedCompare={comparator} />
  const {result} = comparator
  if (result.type === 'same') return <DisplayBothValue value={result.both.value} />
  if (result.type === 'differ') return (
    <>
      <DisplayLeftValue value={result.left.value} />
      <DisplayRightValue value={result.right.value} />
    </>
  )
  if (result.type === 'array') return <DisplayArrayValue value={result} collapse={false} />
  if (result.type === 'object') return <DisplayObjectValue value={result} collapse={false} />
  return (
    <pre>{JSON.stringify(comparator, null, 2)}</pre>
  )
}

export default DisplayDifference